import React from "react";
import ContentLoader from "react-content-loader";

import AppContext from "../../context";

import styles from "./Card.module.scss";

function Card({
	id,
	title,
	imageUrl,
	price,
	onFavorite,
	onPlus,
	favorites = false,
	loading = false,
}) {
	const { isItemAdded } = React.useContext(AppContext);
	const obj = { id, parentId: id, title, imageUrl, price };
	const [isFavorite, setIsFavorite] = React.useState(favorites);

	const onClickPlus = () => {
		onPlus(obj);
	};

	const onClickFavorite = () => {
		onFavorite(obj);
		setIsFavorite(!isFavorite);
	};

	return (
		<div className={styles.card}>
			{loading ? (
				<ContentLoader
					speed={2}
					width={155}
					height={250}
					viewBox="0 0 155 255"
					backgroundColor="#f3f3f3"
					foregroundColor="#ecebeb">
					<rect x="0" y="0" rx="10" ry="10" width="150" height="155" />
					<rect x="0" y="165" rx="5" ry="5" width="150" height="15" />
					<rect x="0" y="190" rx="5" ry="5" width="100" height="15" />
					<rect x="0" y="230" rx="5" ry="5" width="80" height="25" />
					<rect x="118" y="223" rx="10" ry="10" width="32" height="32" />
				</ContentLoader>
			) : (
				<>
					{onFavorite && (
						<div className={styles.favorite} onClick={onClickFavorite}>
							<img src={isFavorite ? "img/liked.svg" : "img/unliked.svg"} alt="Unliked" />
						</div>
					)}
					<img width="100%" src={imageUrl} alt="Sneakers" />
					<h5>{title}</h5>
					<div className="d-flex justify-between align-center">
						<div className="d-flex flex-column">
							<span>Цена:</span>
							<b>{price} руб.</b>
						</div>
						{onPlus && (
							<img
								className={styles.plus}
								onClick={onClickPlus}
								src={isItemAdded(id) ? "img/btn-checked.svg" : "img/btn-plus.svg"}
								alt="plus"
							/>
						)}
					</div>
				</>
			)}
		</div>
	);
}

export default Card;
